import { MouseEvent, useState } from 'react';
import { Link } from 'react-router-dom';
import Modal from '../subComponents/modals/Modal';

const description = [
   {
      title: "L'orthoplastie",
      description: (
         <>
            <p>L'orthoplastie est une orthse en silicone moule sur mesure et destine :</p>
            <ul>
               <li>Corriger une dviation (Hallux valgus, orteils en griffes..)</li>
               <li>Protger une zone douloureuse</li>
            </ul>
         </>
      ),
   },
   {
      title: "L'orthonyxie",
      description: (
         <p>
            L'orthonyxie est une orthse unguale destine  corriger la courbure de l'ongle afin
            d'viter la rapparition d'un ongle incarn.
         </p>
      ),
   },
];

const FootCare = () => {
   const [modalVisible, setModalVisible] = useState(false);
   const [dataForModal, setDateForModal] = useState(description[0]);

   const renderModal = (index: number, e: MouseEvent<HTMLButtonElement>) => {
      e.stopPropagation();
      setDateForModal(description[index]);
      setModalVisible(true);
   };

   return (
      <section
         className='footcare-container'
         onClick={() => {
            setModalVisible(false);
         }}
      >
         <section className='description'>
            <p>
               Le soin de pied (pdicure mdicale) au cabinet ou  <span>domicile</span> comprend la
               prise en charge esthtique et/ou thrapeuthique de diffrentes{' '}
               <Link to='/pathologie'>pathologies</Link> lies directement aux atteintes spcifiques
               du pied. Parfois, un appareillage spcifique via{' '}
               {description.map((item, index) => {
                  return (
                     <>
                        <button
                           key={item.title}
                           onClick={(e) => {
                              renderModal(index, e);
                           }}
                        >
                           {item.title}
                        </button>
                        ,{' '}
                     </>
                  );
               })}
               ou <Link to='/semelle-fonctionnelle'>semelles fonctionnelles</Link> sera ncessaire
               pour viter la chronicit de ces pathologies.
            </p>
         </section>

         <Modal data={dataForModal} modalVisible={modalVisible} close={setModalVisible}></Modal>

         <section className='diabetic'>
            <h4>En cas de diabte</h4>
            <p>
               Le patient diabtique doit se rendre au moins 2x par an chez le podologue afin de
               vrifier les ventuelles complications lies au diabte.
            </p>
            <p>
               En effet, un diabte mal quilibr peut donner certaines complications:{' '}
               <span>Une neuropathie sensitive</span> (absence de douleur),{' '}
               <span>Une arthriopathie</span> (retard dans la cicatrisation),{' '}
               <span>Des dformations osseuses</span>.
            </p>
            <section className='diabetic-advices'>
               <ul>
                  <li>
                     Un diabte mal quilibr peut donner certaines complications (LA NEUROPATHIE
                     SENSITIVE (absence de douleur), UNE MAUVAISE CIRCULATION (retard dans la
                     cicatrisation), DES DFORMATIONS osseuses,
                  </li>
                  <li>
                     Regarder tous les jours ses pieds (si pas possible, demander  un proche),
                     regarder si pas de plaies
                  </li>
                  <li>Ne pas marcher pieds nus quelles que soient les circonstances</li>
                  <li>Ne coupez pas vos ongles seuls</li>
                  <li>Signaler toute lsion ou coloration suspecte</li>
                  <li>Appliquer une crme hydratante tous les jours sur vos pieds</li>
                  <li>Faites appel  votre podologue pour soigner vos cors, durillons</li>
                  <li>Attention aux sources de chaleur. N'utilisez pas de produits agressifs.</li>
                  <li>
                     Aprs la douche, scher correctement vos pieds et insister dans les espaces
                     entre les orteils pour viter l'apparition de mycoses
                  </li>
                  <li>
                     Changer de chaussettes tous les jours. Il est fortement conseill d'utiliser
                     des chaussettes en coton et sans couture pour viter les blessures
                  </li>
               </ul>
            </section>
         </section>
      </section>
   );
};

export default FootCare;
