import { useState, useRef, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';

const diagSteps = [
   {
      title: 'Anamnese',
      description:
         "L'anamnse permet de connatre le motif de la plainte, l'historique du patient, d'analyser ses diffrentes chaussures",
   },
   {
      title: 'Analyse statique',
      description:
         "L'analyse statique permet de vrifier les diffrentes compensations (paules, dos, hanche, genoux, pied)",
   },
   {
      title: 'Examen palpatoire',
      description:
         "L'examen palpatoire permet la mise en vidence de certaines zones douloureuses afin de mieux cibler le traitement.",
   },
   {
      title: 'Mesures biomtriques',
      description:
         "Ces mesures permettent d'en apprendre plus sur les diffrentes mobilits du patient, sur la position idale de son pied et serviront pour la modlisation de la semelle sur logiciel 3D.",
   },
   {
      title: 'Analyse dynamique (marche, course)',
      description:
         "L'analyse de marche et course est une analyse de la cinmatique (qualit du mouvement) et cintique (quantification du mouvement) L'analyse de course permet galement de savoir si la technique de course est adapte aux plaintes du patient.",
   },
   {
      title: 'Conseils spcifiques',
      description:
         "Conseils de chaussage, d'entranement, de technique de course, d'chauffement, d'tirement, de renforcement. Souvent, une prise en charge pluridisciplinaire cible sera recommande (kinsithrapie, ostopathie, mdecin du sport)",
   },
];

const dragVariant = {
   show: {
      x: [0, -40, 0],
      transition: {
         delay: 3,
         repeat: 1,
         repeatDelay: 0.1,
         duration: 0.5,
         type: 'spring',
      },
   },
};

const Diagnostic = () => {
   const [carouselWidth, setCarouselWidth] = useState(0);
   const carousel = useRef<HTMLDivElement>(null);

   useEffect(() => {
      if (carousel.current) {
         const { scrollWidth, offsetWidth } = carousel.current;
         setCarouselWidth(scrollWidth - offsetWidth);
      }
   }, []);

   return (
      <>
         <h3>Semelle fonctionnel</h3>
         <section className='anamnese'>
            <section className='anamnese-steps'>
               <motion.section
                  className='carousel'
                  whileTap={{ cursor: 'grabbing' }}
                  ref={carousel}
               >
                  <motion.ol
                     drag='x'
                     dragConstraints={{ right: 0, left: -carouselWidth }}
                     animate='show'
                     variants={dragVariant}
                     className='inner-carousel'
                  >
                     {diagSteps.map((step, index) => {
                        return (
                           <li key={step.title} className='item'>
                              <h5>
                                 {index + 1}) {step.title}
                              </h5>
                              <p>{step.description}</p>
                           </li>
                        );
                     })}
                  </motion.ol>
               </motion.section>
            </section>
            <section className='description'>
               <p>
                  Les semelles fonctionnelles permettent de corriger biomcaniquement la position du
                  pied et d'aligner les axes des membres infrieurs. Elles ont pour but de rtablir
                  la fonction normale du pied durant le droulement du pas afin d'viter certaines
                  compensations nfastes.
               </p>
               <p>
                  Au cabinet, elles sont fabriques <span>sur-mesure</span> via un scanner et
                  logiciel <span>3D</span>. Les matriaux utiliss pour les semelles varient en
                  fonction de l'ge et de l'activit du patient.
               </p>
               <p>
                  <span>L'examen biomcanique</span> en cabinet permet de comprendre le
                  fonctionnement global du patient et de reconnatre si oui ou non un appareillage
                  via semelles fonctionnelles peut aider dans sa{' '}
                  <Link to='/pathologie'>pathologie</Link>. Cet examen biomcanique se droule en
                  plusieurs tapes :
               </p>
            </section>
         </section>
      </>
   );
};

export default Diagnostic;
