import { useMemo, useRef, useEffect, useState } from 'react';

//*Import dependencies
import { AnimatePresence, motion } from 'framer-motion';

const craftStepsVariant = {
   visible: (i: number) => ({
      opacity: 1,
      y: i * 30,
      x: 0,
      transition: {
         delay: i * 0.3,
         ease: [0.6, 0.1, -0.05, 0.95],
      },
   }),
   hidden: (i: number) => ({ opacity: 0, x: '-500vh', transition: { duration: i * 0.3 } }),
};

const craftSteps = [
   {
      title: "l'empreinte",
      text: "sera faite en position corrige du pied (position neutre de rfrence). Des repres anatomiques y seront pralablement poss afin d'tre le plus prcis possible dans les corrections ajoutes. Le scanner utilis est un scanner 3D qui permet de prendre l'ensemble du pied et permet d'tre plus prcis dans les corrections.",
   },
   {
      title: 'la Conceptualisation',
      text: 'La conceptualisation de la paire de semelles sera faite via un logiciel 3D',
   },
   { title: 'Usinage', text: 'PHOTO' },
   {
      title: 'Finitions',
      text: 'Adaptation de la semelle en fonction des spcifits du patient + PHOTP',
   },
];

const Crafing = () => {
   const [liVisible, setLiVisible] = useState(false);
   const [olHeightContainer, setOlHeightContainer] = useState<number>();

   const olElement = useRef<HTMLOListElement>(null);

   //** without react-intersection-observer npm */
   useEffect(() => {
      const observer = new IntersectionObserver(
         (entries) => {
            const entry = entries[0];
            setLiVisible(entry.isIntersecting);
            if (entry.boundingClientRect.height > 10)
               setOlHeightContainer(entry.boundingClientRect.height);
         },
         { threshold: 0.4 }
      );
      if (olElement.current) observer.observe(olElement.current);
   }, []);

   const renderLi = useMemo(() => {
      return craftSteps.map((step, i) => {
         return (
            <motion.li
               variants={craftStepsVariant}
               custom={i}
               initial='hidden'
               animate='visible'
               exit='hidden'
               key={step.title}
            >
               {step.title}
               <section className='craft-modal'>{step.text}</section>
            </motion.li>
         );
      });
   }, []);

   return (
      <section className='crafting'>
         <h3>Confection de la semelle</h3>
         <section className='description'>
            <p>
               Une fois l'examen biomcanique termin, si le port d'une paire de semelles est
               recommand, (le podologue) passera  la ralisation de celle-ci.
            </p>
            <p>
               Les semelles proposes en cabinet sont des semelles <span>CAD-CAM </span>
               (conception assiste par ordinateur - fabrication assiste par des machines).
            </p>
            <p>
               Ce type de semelles permet d'avoir un contrle au millimtre prs des diffrentes
               corrections voulues et sont parfaitement reproductibles.
            </p>
         </section>
         <section className='craft-steps'>
            <motion.ol
               ref={olElement}
               initial='show'
               style={{ height: olHeightContainer ? olHeightContainer : 'auto' }}
            >
               <AnimatePresence>{liVisible && renderLi}</AnimatePresence>
            </motion.ol>
         </section>
         <section className='next-steps'>
            <p>La suite et le suivi: </p>
            <ul>
               <li>
                  Il faut compter deux semaines aprs l'examen biomcanique pour la remise de
                  semelles.
               </li>
               <li>
                  Une rvision de vos semelles dans les 3 mois aprs leur ralisation est comprise
                  dans le prix. Elle permettra de vrifier.
               </li>
               <li> Un bilan de contrle 1 fois par an est conseill.</li>
            </ul>
         </section>
      </section>
   );
};

export default Crafing;
